<template>
  <div class="contact"> 
    <div class="textecontacte">
      <h1 class="negreta">Contacte</h1>
      <p class="textepetit">Subscriu-te per rebre les √∫ltimes novetats i notificacions directament al teu correu electr√≤nic!</p>
      <p class="textepetit">El ratol√≠ est√† deshabilitat, pots utilitzar el teclat per omplir el formulari.</p>
  </div>

  <div class="form-container" :style="isDisabled ? { pointerEvents: 'none' } : {}">
    
    
    <b-card class="form-card" header="Formulari Newsletter">
      <p v-if="timerActive">El formulari es deshabilitar√† en <strong>{{ countdown }}</strong> segons.</p>
      <b-form @submit="onSubmit" @reset="onReset" v-if="show">
        <!-- Nom -->
        <b-form-group id="input-group-1" label="Nom" label-for="input-1">
          <b-form-input
            id="input-1"
            v-model="form.firstName"
            placeholder="Escriu el teu nom"
            required
          ></b-form-input>
        </b-form-group>

        <!-- Cognoms -->
        <b-form-group id="input-group-2" label="Cognoms" label-for="input-2">
          <b-form-input
            id="input-2"
            v-model="form.lastName"
            placeholder="Escriu els teus cognoms"
            required
          ></b-form-input>
        </b-form-group>

        <!-- Email -->
        <b-form-group id="input-group-3" label="Email" label-for="input-3">
          <b-form-input
            id="input-3"
            v-model="form.email"
            type="email"
            placeholder="Escriu el teu email"
            required
          ></b-form-input>
        </b-form-group>

        <!-- Codi Postal -->
        <b-form-group id="input-group-4" label="Codi Postal" label-for="input-4">
          <b-form-input
            id="input-4"
            v-model="form.postalCode"
            placeholder="Escriu el teu codi postal"
            required
          ></b-form-input>
        </b-form-group>

        <!-- Botones -->
        <b-button type="submit" variant="success" block class="subscribe-button">Subscriu</b-button>
      </b-form>
    </b-card>
  </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        firstName: '',
        lastName: '',
        email: '',
        postalCode: ''
      },
      show: true,
      isDisabled: false, // Controla si los campos est√°n habilitados o no
      countdown: 10, // Contador de 10 segundos
      timerActive: true // Bandera para mostrar el temporizador
    };
  },
  methods: {
    onSubmit(event) {
      event.preventDefault();
      alert(`Subscribed with: ${JSON.stringify(this.form)}`);
    },
    startTimer() {      
      this.isDisabled = true; // Deshabilita el rat√≥n
      const timerInterval = setInterval(() => {
        if (this.countdown > 0) {
          this.countdown -= 1;
        } else {
          clearInterval(timerInterval);
          this.timerActive = false; // Detiene el mensaje del temporizador
        }
      }, 1000);
    },
    onReset(event) {
      event.preventDefault();
      this.form.firstName = '';
      this.form.lastName = '';
      this.form.email = '';
      this.form.postalCode = '';
    }
  },
  mounted() {
    this.startTimer();
  }
};
</script>

<style scoped>
/* Fondo y contenedor */
.form-container {
  display: flex;
  justify-content: center;
  height: auto;
  background-color: #ffffff;
  padding: 0 2% 2% 2%;
}

/* Estilo de la tarjeta del formulario */
.form-card {
  width: 100%;
  max-width: 400px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  background-color: #f8f8f8;
  padding: 20px;
  margin: 0 auto;  /* Aqu√≠ puedes modificar el margen */
}

/* Campos de entrada */
b-form-group {
  margin-bottom: 1.5rem;
}

/* Botones */
.subscribe-button {
  margin-top: 20px;
  background-color: #0066ff;  /* Aqu√≠ puedes cambiar el color del fondo */
  border-color: #002a68; /* Color del borde */
  color: #fff; /* Color del texto */
}

/* Responsividad */
@media (max-width: 576px) {
  .form-card {
    width: 100%;
    padding: 15px;
  }
}
</style>