<template>
  <div class="albums">
    <h1 class="moviment">Millors Albums</h1>
    <p class="moviment textepetit">Millors albums segons <span class="cursiva">Pitchfork</span> de la derrera d√®cada.</p>
    <p class="moviment textepetit">Posa't a sobre d'una imatge per veure m√©s informaci√≥</p>
    <p class="moviment textepetit ">Si l'animaci√≥ no atura al marxar, apretar <span class="subratllat"><b-link href="/" alt="Home">aqu√≠</b-link></span></p>

    <div class="image-container">
      <div
        v-for="(image, index) in images"
        :key="index"
        class="floating-image"
        :style="randomPositionStyle(index)"
      >
        <b-img
          :src="image"
          :alt="'Image ' + (index + 1)"
          class="chaotic-image"
          v-b-popover.hover.top="popovers[index]"
          @mouseover="randomizePositions"
        />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // Lista de 15 im√°genes espec√≠ficas
      images: Array.from({ length: 15 }, (_, i) => require(`@/assets/imagen${i + 1}.jpg`)),
      // Textos personalizados para cada imagen
      popovers: [
        "Eternal Sunshine - Ariana Grande",
        "MOTOMAMI - ROSAL√çA",
        "Melodrama- Lorde",
        "BRAT - Charli XCX",
        "Lo Que Te Conte Mientras Te Hac√≠as La Dormida - La Oreja De Van Gogh",
        "HIT ME HARD ANS SOFT - Billie Eilish",
        "El Mal Querer - ROSAL√çA",
        "Sweetener - Ariana Grande",
        "Short n' Sweet - Sabrina Carpenter",
        "La Joia - Bad Gyal",
        "AMIGUES I AUTOTUNE - FADES",
        "ARTPOP - Lady Gaga",
        "Bodhiria - Judeline",
        "Eusexua - FKA Twigs",
        "Si Abro Los Ojos No Es Real - Amaia",
      ],
      positions: [],
    };
  },
  mounted() {
    this.randomizePositions();
    setInterval(this.randomizePositions, 2000);
  },
  methods: {
    randomizePositions() {
      this.positions = this.images.map(() => ({
        top: Math.random() * 80 + "%",
        left: Math.random() * 80 + "%",
        transform: `rotate(${Math.random() * 360}deg)`,
      }));
    },
    randomPositionStyle(index) {
      const position = this.positions[index] || {};
      return {
        position: "absolute",
        transition: "all 1s ease",
        ...position,
      };
    },
  },
};
</script>

<style>
.albums {
  font-family: Arial, sans-serif;
  overflow: hidden;
  position: relative;
  width: 100vw;
  height: 100vh;
  background-color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.image-container {
  position: relative;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.floating-image {
  pointer-events: auto;
}

.chaotic-image {
  width: 100px;
  height: 100px;
  object-fit: cover;
  border-radius: 50%;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
}

.subratllat{
  color: #1183FF;
}


@keyframes shake {
  0% { transform: translate(0, 0); }
  25% { transform: translate(-5px, 5px); }
  50% { transform: translate(5px, -5px); }
  75% { transform: translate(-5px, -5px); }
  100% { transform: translate(0, 0); }
}

.moviment, .icones, .headerimage, .headerlink {
  animation: shake 0.5s infinite;
}
</style>
